.login-container {
  margin-top: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;

  .login-request {
    color: $white;
    font-weight: $bolder;
  }

  .button {
    border-radius: 4px;
    border: solid 1px $dark-gray;
    text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.4);
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.4),
      0 1px 1px rgba(0, 0, 0, 0.2);
    background: $gray;
    color: $white;
    padding: 10px 14px;
    text-decoration: none;
    width: fit-content;
    margin: 10px 0;
  }
}
